.Badge {
  /* background: #ffffff; */
  background: transparent;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);
  border-radius: 8px 8px 8px 8px;
  overflow: hidden;
  height: 380px;
  margin-bottom: 10px;
  padding: 1rem;
  width: 100%;
}
.Badge__h1 {
  color: #f4f4f7;
}
.Badge__border {
  border: 3px solid #23989e !important;
}

.Badge__header {
  padding: 0.5rem 0;
  height: 80px;
  background: #1b1b25;
  display: flex;
  justify-content: center;
}

.Badge__section-name {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 0;
}

.Badge__section-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.5rem 0;
  background: #f4f4f7;
  border-radius: 8px 8px 8px 8px;
}

.Badge__avatar {
  border-radius: 50%;
  margin-right: 1rem;
  width: 120px;
  height: 120px;
}

.Badge__footer {
  height: 54px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #98ca3f;
  font-weight: bold;
  font-style: italic;
}

.Badge__footer-icon-social{
  margin-bottom: 2em;
}

.Badge__domain {
  text-decoration: underline;
  font-weight: bold;
}
