:root {
  --surface-color: #fff;
  --curve: 40;
}

* {
  box-sizing: border-box;
}
ul li::before {
  content: attr(data-icon);
  /* Make slightly larger than the li font-size
    but smaller than the li grid-gap */
  font-size: 1em;
}
ol,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-gap: 1rem;
}
li {
  display: grid;
  grid-template-columns: 0 1fr;
  grid-gap: 1.75em;
  align-items: start;
  font-size: 1rem;
  line-height: 1.25;
}

.Briefcase__cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(600px, 1fr));
  gap: 2rem;
  margin: 4rem 5vw;
  padding: 0;
  list-style-type: none;
}

.BriefcaseItem__card {
  position: relative;
  display: block;
  height: 100%;
  /* border-radius: calc(var(--curve) * 1px); */
  overflow: hidden;
  text-decoration: none;

  border-radius: 0% 0% 0% 0% / 0% 0% 0% 0%;
  box-shadow: 10px 10px rgba(0, 0, 0, 0.15);
  transition: all 0.4s ease;
  background-color: rgb(255, 255, 255);
}
.BriefcaseItem__card:hover {
  /* border-radius: 0% 0% 50% 50% / 0% 0% 5% 5% ; */
  border-radius: calc(var(--curve) * 1px);

  box-shadow: 5px 5px rgba(0, 0, 0, 0.25);
}
.BriefcaseItem__image {
  max-width: 100%;
}

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  .Briefcase__cards {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  }
  .BriefcaseItem__card-image {
    /* height: 300px !important; */
    margin-bottom: 100px;
  }
  .BriefcaseItem__card-header {
  
    border-radius: 0% !important;
  }
  .BriefcaseItem__image {
    max-width: 100%;
  }
  ol,
  ul {
    grid-gap: 0rem;
  }
}
.BriefcaseItem__card-image {
  width: 100%;
  height: auto;
}

.BriefcaseItem__card-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 0;
  border-radius: calc(var(--curve) * 1px);
  background-color: var(--surface-color);
  transform: translateY(100%);
  transition: 0.2s ease-in-out;
}

.BriefcaseItem__card:hover .BriefcaseItem__card-overlay {
  transform: translateY(0);
}

.BriefcaseItem__card-header {
  position: relative;
  display: flex;
  align-items: center;
  gap: 2em;
  padding: 2em;
  border-radius: calc(var(--curve) * 1px) 0 0 0;
  background-color: var(--surface-color);
  transform: translateY(-100%);
  transition: 0.2s ease-in-out;
}

.BriefcaseItem__card-arc {
  width: 80px;
  height: 80px;
  position: absolute;
  bottom: 100%;
  right: 0;
  z-index: 1;
}

.BriefcaseItem__card-arc path {
  fill: var(--surface-color);
  d: path("M 40 80 c 22 0 40 -22 40 -40 v 40 Z");
}

.BriefcaseItem__card:hover .BriefcaseItem__card-header {
  transform: translateY(0);
}

.BriefcaseItem__card-thumb {
  flex-shrink: 0;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.BriefcaseItem__card-title {
  font-size: 1em;
  margin: 0 0 0.3em;
  color: #6a515e;
}

.BriefcaseItem__card-tagline {
  display: block;
  margin: 1em 0;
  font-size: 0.8em;
  color: #d7bdca;
}

.BriefcaseItem__card-status {
  font-size: 0.8em;
  color: #d7bdca;
}

.BriefcaseItem__card-description {
  padding: 0 2em 2em;
  margin: 0;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
}

.BriefcaseItem__description-span {
  text-align: left;
}
